<template>
	<box-item title="信息直报" show-sub-title sub-title="统计" @hand-sub-title="hand2More">
		<operators-dynamic :categoryList="[1, 2]" :farmAreaInfo="farmAreaInfo"></operators-dynamic>
	</box-item>
</template>

<script>
import BoxItem from '@/components/common/BoxItem';
import operatorsDynamic from '@/components/common/operatorsDynamic';
import {areaLevelCorAreaId} from '@/common/fn';

export default {
	name: 'OperatorsDynamic',
	data() {
		return {
			farmAreaInfo: areaLevelCorAreaId()
		};
	},
	created() {},
	mounted() {},
	methods: {
		hand2More() {
			this.$router.push({name: 'farmRecordListOther'});
		}
	},
	components: {
		BoxItem,
		operatorsDynamic
	}
};
</script>

<style scoped lang="less"></style>



